.slick-arrow{
    position: absolute;
    top: 50%;
    z-index: 100;
}
.slick-prev{
    left: 0;
}
.slick-next{
    right: 0;
}

.portfolioWrap div h3{
    text-align: center;
    font-size: 30px;
    margin-top: 40px;
}
.portfolioWrap div p{
    text-align: center;
}
.portfolio{
    display: flex;
}
.portfolio>.left img{
    width: 600px;
    margin-top: 20px;
    margin-left: 90px;
    margin-bottom: 20px;
}
.portfolio>ul{
    color: black;
    margin-left: 20px;
    width: 300px;
    background-color: rgba(256, 256, 256,0.3);
    height: 430px;
    padding: 20px;
    box-sizing: border-box;
    border-radius: 20px;
    margin-top: 100px;
    display: flex;
    flex-direction: column;
    justify-content: top;
}
.portfolio>ul>h4{
    
    font-size: 20px;
    margin-bottom: 30px;
}
.portfolio>ul>li{
    margin-bottom: 10px;
    font-weight: bold;
}
.portfolio>ul>li:nth-child(3){
    margin-bottom: 30px;
}
.portfolio>ul>li:nth-child(4){
    margin-bottom: 30px;
}
.portfolio>ul .color{
    background: white;
    width: 110px;
    box-sizing: border-box;
    padding: 10px;
    border-radius: 10px;
}
.portfolio>ul .skill {
    display: flex;
    flex-wrap: wrap;
    margin-top: 10px;
}
.portfolio>ul .skill li{
    margin-right: 20px;
}
.portfolio>ul .skill li{
    width: 35px;
}
.portfolio>ul .skill li img{
    width:100%
}
.portfolio>ul .skill li:last-child img,
.portfolio>ul .skill li:nth-child(4) img,
.portfolio>ul .skill li:nth-child(5) img{
    width:120%;
}
.portfolio>ul li span{
    font-size: 14px;
    font-weight: normal;
}
.portfolio>ul li:last-child span{
    display: block;
    margin-top: 5px;
    font-size: 12px;
}
