.todoItem{
    border-bottom: 1px solid white ;
    display: flex;
    line-height: 3;
    padding: 0 30px;
    border-radius: 10px;
    margin-top: 10px;
    cursor: pointer;
}

.remover{
    margin-right: 20px;
    color: yellow;
    font-weight: bold;
    opacity: 0;
}
.todoItem:hover .remover{
    opacity: 1;
}
.checked{
    text-decoration: line-through;
    color: #ccc;
}